*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.header {
  position: relative;
  width: 100vw;
  height: 8vh;
}
.header .main_header {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #eff2f2;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 0 10px #0c0c0c;
  filter: drop-shadow(0 3px 5px #595959);
  box-shadow: inset 0 5px 10px #0c0c0c;
  z-index: 10;
}
.header .main_header img {
  height: 6vh;
  flex: 1 6vh 0;
  margin-right: 1rem;
  filter: drop-shadow(0 5px 5px #262626);
  border-radius: 50%;
}
.header .main_header h1 {
  color: #3bbfbf;
}
.header .filler {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  background-color: #77d8d8;
}

.content {
  height: 84vh;
  background-color: #77d8d8;
  display: flex;
}

.footer {
  height: 8vh;
  width: 100%;
  background-color: #0c0c0c;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer img {
  height: 6vh;
  width: auto;
  margin: 2vw;
  border-radius: 50%;
}
.footer a {
  color: #3bbfbf;
}